import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';

import { DtDetalleInterface } from '../../../shared/ui/datatable/dt-detalle.interface';

@Component({
    templateUrl: './detalle-operador.component.html'
})
export class DetalleOperadorComponent implements DtDetalleInterface {
    @Input() data: any;

    constructor(private router: Router) { }

    Detalle(data: any): void {
        this.router.navigate(['detalle', data._id]);
    }

    Editar(data: any): void {
        this.router.navigate(['editar', data._id]);
    }

    Desactivar(data: any): void {
        this.router.navigate(['desactivar', data._id]);
    }
}



