import {NgModule} from '@angular/core';
import {SmartadminModule} from '../../shared/smartadmin.module'
import {SmartadminDatatableModule} from '../../shared/ui/datatable/smartadmin-datatable.module';

import { PersonalService } from './../shared/personal.service';

import {routing} from './operadores.routing';
import {OperadoresComponent} from './operadores.component';
import {DetalleOperadorComponent} from './detalle/detalle-operador.component';
import { OperadorFormComponent } from './operador-form/operador-form.component';
import { FormContainerModule } from './../../+itinerarios/shared/form-container/form-container.module';
import { SmartadminInputModule } from './../../shared/forms/input/smartadmin-input.module';


@NgModule({
    declarations: [
        OperadoresComponent,
        DetalleOperadorComponent,
        OperadorFormComponent
    ],
    imports: [
        SmartadminModule,
        SmartadminDatatableModule,
        routing,
        SmartadminInputModule,
        FormContainerModule
    ],
    entryComponents: [
        DetalleOperadorComponent
    ],
    providers: [PersonalService]
  })
  export class OperadoresModule {}



