import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { PersonalService } from '../../shared/personal.service';
import { personalValidationOptions } from '../../shared/personal-validation-options';

declare var $: any;

@Component({
  selector: 'app-operador-form',
  templateUrl: './operador-form.component.html',
  styles: []
})
export class OperadorFormComponent implements OnInit {
  mode: string;
  operador: any = {};
  isChanginPassword = false;
  validationOptions = personalValidationOptions;

  private $form;
  private lastEmail = '';

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private personalService: PersonalService
  ) {
    this.mode = this.route.snapshot.params.id ? 'Editar' : 'Crear';
  }

  ngOnInit() {
    this.operador = {
      ...this.personalService.personalModel,
      roles: [3]
    };
    this.route.params.subscribe(params => {
      this.render(params.hasOwnProperty('id') ? params['id'] : false);
    });
  }

  render(id = false) {
    if (id) {
      this.personalService.getPersonal(id)
        .subscribe((data: any) => {
          this.operador = data.result;
        });
    }
  }

  update(ev) {
    this.operador[ev.target.name] = ev.target.value;
  }

  save(operador) {
    if (!operador.hasOwnProperty('_id')) {
      this.personalService.createPerfil(operador)
        .subscribe((data: any) => this.handleSuccess(data.result));
    } else {
      if (typeof operador.newPassword !== 'undefined') {
        operador['password'] = operador.newPassword;
      } else {
        delete operador['password'];
      }
      this.personalService.updatePerfil(operador)
        .subscribe((data: any) => this.handleSuccess(data.result));
    }
  }

  handleSuccess(status) {
    if (status.success) {
      this.router.navigate(['usuarios', 'operadores']);
    } else {
      if (status.code === 11000) {
        this.$form.find('input[name=email]').data('last-mail', this.operador.email);
        this.$form.data('bootstrapValidator').revalidateField('email');
      }
    }
  }

  onValidationSuccess(e) {
    this.$form = $(e.currentTarget);
    if (e.type === 'success') {
      this.save(this.operador);
    }
  }
}



