import { PersonalService } from './../shared/personal.service';
import { Component, OnInit, Type, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Subject, Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { DetalleOperadorComponent } from './detalle/detalle-operador.component';

declare var $: any;

@Component({
  selector: 'operadores',
  templateUrl: './operadores.component.html',
  styles: []
})
export class OperadoresComponent implements OnInit {
  itemsObs: Subject<any> = new Subject();
  dtTrigger: Subject<any> = new Subject();
  dcomp: Type<any> = DetalleOperadorComponent;

  options = {
    dom: 'Bfrtip',
    columnDefs: [ {
      targets: [0, 5],
      orderable: false
    } ],
    order: [[4, 'desc']],
    colReorder: true
  };

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private personalService: PersonalService
  ) { }

  ngOnInit() {
    this.personalService.getOperadores()
      .subscribe((data: any) => {
        this.itemsObs.next(data.result);
      });
  }

  Editar(data: any): void {
    this.router.navigate(['editar', data._id], {relativeTo: this.route});
  }
}



