import {ModuleWithProviders} from '@angular/core'
import {RouterModule, Routes} from '@angular/router';

import {OperadoresComponent} from './operadores.component';
import { OperadorFormComponent } from './operador-form/operador-form.component';

export const routes: Routes = [
    {
        path: '',
        component: OperadoresComponent,
        data: {pageTitle: ''}
    },
    {
        path: 'crear',
        component: OperadorFormComponent,
        data: {pageTitle: 'Crear Operador'}
    },
    {
        path: 'editar/:id',
        component: OperadorFormComponent,
        data: {
            pageTitle: 'Editar Operador'
        }
    }
];

export const routing = RouterModule.forChild(routes)



